body {
  margin: 0;
  padding: 0;
  font-family: Arial,Helvetica,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center {
  text-align: center;
}

.mb20 {
  margin-bottom: 20px;
}

.chmod-header {
  text-align: center;
  padding: 15px;
  margin-bottom: 50px; }
  .chmod-header h1 {
    font-size: 60px;
    color: #333f48; }
    .chmod-header h1 a {
      font-size: 14px;
      color: #8b949a;
      text-decoration: none; }
      .chmod-header h1 a:visited {
        color: #8b949a; }
      .chmod-header h1 a:hover {
        color: #333f48; }
  .chmod-header p {
    color: #8b949a;
    font-size: 30px; }
  @media screen and (max-width: 800px) {
    .chmod-header {
      margin-bottom: 0; }
    .chmod-header h1 {
      font-size: 30px; }
    .chmod-header p {
      font-size: 15px; } }

.checkbox-container {
  text-align: right;
  margin-bottom: 25px; }
  .checkbox-container label {
    font-size: 24px;
    font-weight: bold;
    color: #333f48;
    cursor: pointer; }
  .checkbox-container input {
    margin-left: 10px;
    vertical-align: middle; }
  @media screen and (max-width: 800px) {
    .checkbox-container {
      text-align: right;
      margin-bottom: 10px; }
      .checkbox-container label {
        font-size: 18px; } }

.chmod-select {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .chmod-select h2 {
    margin-bottom: 20px;
    font-size: 35px;
    color: #333f48; }
  @media screen and (max-width: 800px) {
    .chmod-select {
      margin: 0 auto;
      padding-top: 15px;
      display: flex;
      align-items: center;
      justify-content: space-around;
      border-bottom: 1px solid #8b949a; }
      .chmod-select h2 {
        font-size: 24px; } }

.fire-highlight {
  font-size: 14px;
  max-width: 100%;
  overflow-x: scroll;
  background-color: #f5f5f5;
  border-radius: 4px;
  padding: 10px;
  margin: 10px; }

.use-age {
  padding: 0 20px; }

.calculator {
  max-width: 1200px;
  margin: 0 auto; }
  .calculator-main {
    display: flex;
    justify-content: space-around;
    margin-bottom: 40px;
    padding: 0 20px; }
  .calculator-display {
    text-align: center;
    margin-bottom: 20px; }
    .calculator-display h2 {
      margin-bottom: 20px; }
    .calculator-display__input {
      display: flex;
      justify-content: center; }
      .calculator-display__input input {
        outline: none;
        width: 100px;
        height: 40px;
        border: 1px solid #8b949a;
        border-radius: 4px;
        padding: 2px;
        text-align: center;
        font-size: 18px;
        color: #333f48;
        letter-spacing: 1px;
        margin: 0 30px; }
  @media screen and (max-width: 800px) {
    .calculator-main {
      width: 100%;
      display: block;
      margin-bottom: 20px; } }

